import common from '../../common/common.js'
export default {
	data() {
		return {
			swiperList: []
		}
	},
	onLoad() {
		this._getSwiperData()
	},
	methods: {
		_getSwiperData() {
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: common.api_base_url + '/index/carousel/list',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: common.qq
				},
				method: 'POST',
				success: ((res) => {
					if (res.data.status === 200 && res.data.msg === 'OK') {
						this.swiperList = res.data.data
					} else {
						this.swiperList = []
					}
				}),
				fail: ((err) => {
					console.log(err)
					this.swiperList = []
				}),
				complete: (() => {
					uni.hideLoading()
				})
			})
		}
	}
}
